.body__description_200 {
    font-size: 2em;
    font-family: '한국기계연구원_bold', serif;
    text-align: center;
    margin-bottom: 100px;
    padding-bottom: 50px;
}
.body__description_100 {
    padding-top: 50px;
    font-size: 2em;
    font-family: '한국기계연구원_bold', serif;
    text-align: center;
    margin-bottom: 50px;
}

header {
    background-image: 
        linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0)), 
        linear-gradient(to top, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0)), 
        url("../../images/purpleBackground.png");
    background-repeat: no-repeat;
    background-size: 100% 100%, 100% 100%, auto;
}

.fade-in-section {
    opacity: 0;
    transform: translateY(15vh);
    visibility: hidden;
    transition: opacity 0.6s ease-out, transform 3s ease-out;
    will-change: opacity, visibility;
  }
  
  .fade-in-section.is-visible {
    opacity: 1;
    transform: none;
    visibility: visible;
  }
  


