/* CGMakeModal.css */
.wrapper-modal {
    position: fixed;
    top: 0;
  right: 0;
  bottom: 0;
  left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000; /* 큰 z-index 값 설정 */
  }
  
  .modal {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 90%;
    background-color: rgb(41, 0, 76, 0.8);
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.25);
  }
  
  .modal__content {
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    width: 100%;
    height: 100%;
    overflow-y: auto;
  }

  .modal__content__Canvas__List {
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    width: 100%;
    height: 100%;
    gap: 20px;
  }
  
  .modal-button {
    align-self: flex-end;
    font-size: 1.5rem;
    cursor: pointer;
  }
  
  /* CGCanvas.css */
  .cgCanvas {
    display: flex;
    width: 100%;
    height: 50%;
    margin: 20px 0;
    padding: 20px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.15);
    justify-content: space-evenly;
  }
  
  /* CGItem.css */
  .cgItem {
    width: 100px;
    height: 100px;
    object-fit: contain;
    cursor: grab;
  }
  
  /* CGList.css */
  .cgList {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
  }
  
  .cgListItem {
    width: 100px;
    height: 100px;
    margin: 10px;
  }
  