/* 전체적인 배경 및 글꼴 설정 */
body {
  background-color: rgba(0, 0, 0, 1);
  font-family: Arial, sans-serif;
  color: #fff;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}


.banner {
  height: 100vh;
  position: relative;
  object-fit: contain;
  background-image: url('../images/banner.jpg'); /* 여기에 배경 이미지 URL을 입력하세요. */
  background-repeat: no-repeat;
  background-size: cover; /* 이미지를 요소에 맞게 확장합니다. */
  font-family: '한국기계연구원_bold', serif;
}
.banner::after {
  content: '';
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: 
  linear-gradient(to bottom, rgba(255, 255, 255, 0) 90%, rgba(0, 0, 0, 1)), 
  linear-gradient(to top, rgba(255, 255, 255, 0) 90%, rgba(0, 0, 1, 1));

}

/* 배너 텍스트 설정 */
.banner__title {
  font-size: 3em;
  font-weight: bold;
  padding: 0;
  margin: 0;
  font-family: '한국기계연구원_bold', serif;
  margin-bottom: 150px; 
  
}

.banner__description {
  font-size: 2em;
  padding: 0;
  font-family: '한국기계연구원_bold', serif;
  margin-bottom: 70px; 
}

.banner__contents {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  margin-bottom: 50px; 
}

.fontStyle {
  font-size: 2em;
  font-weight: bold;
  text-align: center;
  padding-top: 3em;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  font-size: 1.2em;
  padding: 1em 0;
  text-align: center;
}

@font-face { 
  font-family: "한국기계연구원_bold";
  src: url("../fonts/한국기계연구원_bold.ttf") format("truetype");
  font-weight: normal;
} 

