/* VideoUploadModal.css */

.wrapper-modal {
    position: fixed;
    inset: 0px;
    background-color: rgba(0, 0, 0, 0.71);
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .modal {
    position: relative;
    max-width: 800px;
    background: #111;
    overflow: hidden;
    border-radius: 8px;
    padding: 40px;
    color: white;
    box-shadow: 0px 3px 5px -1px rgba(0, 0, 0, 0.2), 0px 5px 8px 0px rgba(0, 0, 0, 0.14), 0px 1px 14px 0px rgba(0, 0, 0, 0.12);
    animation: fadeIn 400ms;
  }
  
  .modal-close {
    position: absolute;
    right: 20px;
    top: 20px;
    cursor: pointer;
    color: white;
  }
  
  @keyframes fadeIn {
    from {
      opacity: 0;
      transform: scale(0.5);
    }
    to {
      opacity: 1;
      transform: scale(1);
    }
  }
  