.nav {
    display: flex;
    justify-content: flex-end;
    position: fixed;
    width: 100%;
    padding: 20px 0;
    padding-top: 10px; 
    padding-bottom: 9px; 
    transition-timing-function: ease-in;
    transition: 0.5s;
    z-index: 1000;
    font-family: '한국기계연구원_bold', serif;
}


.nav a {
    color: #fff;
    text-decoration: none;
    font-size: 0.7em;  /* 글자 크기를 조절합니다. */
    font-weight: bold; /* 글자 두꺼워짐 */
    margin-right: 20px; /* 우측에 마진을 추가하여 링크 사이에 공간을 만듭니다. */
}


/* 스크롤이 특정 위치를 넘어갔을 때 적용될 클래스 */
.nav__black {
    background-color: rgba(0, 0, 0, 0.8);
}

.nav__input {
    position: fixed;
    left: 50%;
    transform: translate(-50%, 0);
    background-color: rgba(0, 0, 0, 0.582);
    border-radius: 5px;
    color: white;
    padding: 5px;
    border: none;
}

@font-face { 
    font-family: "한국기계연구원_bold";
    src: url("../fonts/한국기계연구원_bold.ttf") format("truetype");
    font-weight: normal;
} 