.navgation {
    display: flex;
    justify-content: space-evenly;
    align-items: right;
    position: fixed;
    width: 100%;
    padding: 20px 0;
    margin-top: -20px;
    padding-top: 20px; 
    padding-bottom: 20px; 
    transition-timing-function: ease-in;
    transition: 0.5s;
    z-index: 1000;
    font-family: '한국기계연구원_bold', serif;
}

.navgation a {
    color: #fff;
    text-decoration: none;
    font-size: 1.2em;
    font-weight: bold; /* 글자 두꺼워짐 */
}

/* 스크롤이 특정 위치를 넘어갔을 때 적용될 클래스 */
.navgation__black {
    background-color: rgba(0, 0, 0, 0.8);
}

.navgation_input {
    position: fixed;
    left: 50%;
    transform: translate(-50%, 0);
    background-color: rgba(0, 0, 0, 0.582);
    border-radius: 5px;
    color: white;
    padding: 5px;
    border: none;
}

@font-face { 
    font-family: "한국기계연구원_bold";
    src: url("../fonts/한국기계연구원_bold.ttf") format("truetype");
    font-weight: normal;
} 