.cgCanvas {
    
    width: 500px; /* or any desired width */
    height: 270px; 
    min-height: 270px;/* or any desired height */
    position: relative;
    overflow: hidden; /* to prevent scrolling within the canvas */
    background-color: rgba(105, 0, 170, 1);
    margin: 20px 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: center;
    border: 1px solid #ccc;
    overflow: auto;
    
}